import React, { Component } from 'react';
import './App.css';


class App extends Component {
  constructor(props){
    super(props);
    this.state={
      turn:1,
      goFirst:true,
      cpu:false,
      player1:'X',
      player2:'O',
      clr1:1,
      clr2:3,
      plays:0,
      again:false,
      ttt:[[0,0,0],[0,0,0],[0,0,0]]
    };
  }
  // Test Styles
  createShine=(cID,cStil,cHigh,cMid,cLow)=>{
    return("@keyframes shine-"+cID+" {"+
      "0% {color: "+cStil+";text-shadow: none;}"+
      "100% {color: "+cLow+";"+
        "text-shadow: 0 0 0.6rem "+cLow+", 0 0 1.5rem "+cMid+","+
          "-0.2rem 0.1rem 1rem "+cMid+", 0.2rem 0.1rem 1rem "+cMid+","+
          "0 -0.5rem 2rem "+cHigh+", 0 0.5rem 3rem "+cHigh+";}}");
  }
  createBlick=(cID,cHigh,cMid,cLow)=>{
    return("@keyframes blink-"+cID+" {"+
      "0%,22%,36%,75% {color: "+cLow+";"+
                      "text-shadow: 0 0 0.6rem "+cLow+", 0 0 1.5rem "+cMid+","+
                      "-0.2rem 0.1rem 1rem "+cMid+", 0.2rem 0.1rem 1rem "+cMid+","+
                      "0 -0.5rem 2rem "+cHigh+", 0 0.5rem 3rem "+cHigh+";}"+
      "28%,33% {color: "+cMid+";"+
               "text-shadow: none;}"+
      "82%,97% {color: "+cHigh+";"+
               "text-shadow: none;}}");
  }
  createFlicker(cID,shineT,blinkTD,blinkTF){
    return(".flicker"+cID+"{animation: shine-"+cID+" "+shineT+"s forwards, blink-"+cID+" "+blinkTD+"s "+blinkTF+"s infinite;}")
  }
  addFlickerStyles=(flicker,shine,blink)=>{
    let css = document.createElement('style'); 
    css.type = 'text/css'; 
    if (css.styleSheet)  
        css.styleSheet.cssText = shine+blink+flicker; 
    else {
      css.appendChild(document.createTextNode(shine)); 
      css.appendChild(document.createTextNode(blink));
      css.appendChild(document.createTextNode(flicker));
    } 
    /* Append style to the tag name */ 
    document.querySelector("head").appendChild(css); 
  }
  setStyles=()=>{
    //1:pink, 2:red, 3:green, 4:yellow
    // Pink
    this.addFlickerStyles(this.createFlicker(1,2,10,1),
    this.createShine(1,"#6b1839","#ff2483","#ff65bd","#ffe6ff"),
    this.createBlick(1,"#ff2483","#ff65bd","#ffe6ff"));
    //Red
    this.addFlickerStyles(this.createFlicker(2,2,10,1),
    this.createShine(2,"#aa0403","#ff0413","#ff459d","#ffefef"),
    this.createBlick(2,"#ff0413","#ff459d","#ffefef"));
    //Green
    this.addFlickerStyles(this.createFlicker(3,2,10,1),
    this.createShine(3,"#186b39","#24ff83","#65ffbd","#e6ffff"),
    this.createBlick(3,"#24ff83","#65ffbd","#e6ffff"));
    //Yellow
    this.addFlickerStyles(this.createFlicker(4,2,10,1),
    this.createShine(4,"#6b3918","#ff8324","#ffbd65","#ffe6ff"),
    this.createBlick(4,"#ff8324","#ffbd65","#ffe6ff"));
  }
  //End Styles
  checkWin=(row,col)=>{
    const{ttt}=this.state;
// Check Row
    let play=ttt[row][col];
    let check=true;
    for(let i=0;i<3;i++){
      check&=(ttt[row][i]===play);
    }
    if(check)return true;
    check=true;
    for(let i=0;i<3;i++){
      check&=(ttt[i][col]===play);
    }
    if(check)return true;
    if(row===col){
      check=true;
      for(let i=0;i<3;i++){
        check&=(ttt[i][i]===play);
      }
      if(check)return true;
    }
    if(row+col===2){
      check=true;
      for(let i=0;i<3;i++){
        check&=(ttt[i][2-i]===play);
      }
      if(check)return true;
    }
    return false;
  }
  declareWinner=(player)=>{
    const{again}=this.state;
    document.querySelector("#win").innerHTML=(player===1?"Player 1":(player===2?"Player 2":"No one (Meaning a 'Draw')"));
    if(!again)this.toggleMenu();
    this.toggleModal();
  }
  resetTds=()=>{
    let tds=document.querySelectorAll("td");
    tds.forEach((td)=>{
      td.classList.remove("alert_green","alert_red");
    });
  }
  legalMoves=()=>{
    let tds=document.querySelectorAll("td:empty");
    tds.forEach((td)=>{
      td.classList.add("alert_green");
    });
    tds=document.querySelectorAll("td:not(:empty)");
    tds.forEach((td)=>{
      td.classList.add("alert_red");
    });
  }
  cpuPlay=()=>{
    const{player2,clr2,plays,ttt}=this.state;
    let tds=document.querySelectorAll("td:empty");
    if(tds.length!==0){
      let index=Math.floor(Math.random() * tds.length);
      let chose=tds[index];
      chose.innerHTML=player2;//(turn===1?player1:player2);
      chose.classList.add("flicker"+clr2);//(turn===1?clr1:clr2));
      let row=chose.parentNode.rowIndex;
      let col=chose.cellIndex;
      ttt[row][col]=player2;//(turn===1?player1:player2);
      if(this.checkWin(row,col)){
        this.declareWinner(2);
      }
      /* else{
        if(plays===8){
          //        It's a draw
          this.declareWinner(-1);
        }
      } */
      this.setState({turn:1,plays:plays+2,ttt:ttt});
    }
    // }else{
    //   //game done
    //   this.declareWinner(-1);
    // }
  }
  tdClick=()=>(e)=>{
    // Check if Draw
    const{turn,cpu,player1,player2,clr1,clr2,ttt,plays}=this.state;
    if(e.target.innerHTML===""){
      this.resetTds();
//    add symbol
      e.target.innerHTML=(turn===1?player1:player2);
      e.target.classList.add("flicker"+(turn===1?clr1:clr2));
      let row=e.target.parentNode.rowIndex;
      let col=e.target.cellIndex;
      ttt[row][col]=(turn===1?player1:player2);
      let check=this.checkWin(row,col);
      if(check){
//      we got a winner
        console.log("Congrates");
        this.declareWinner(turn);
      }else{
//    Add Class
        if(plays===8){
//        It's a draw
          this.declareWinner(-1);
        }
//    Call CPU
        if(cpu){
          this.cpuPlay();
        }else{
          //    Turn change
          this.setState({turn:(turn===1?2:1),plays:plays+1,ttt:ttt});
        }
      }
    }else{
//    Have a symbol
      this.legalMoves();
    }
  }
  createTable = () => {
    let table = []
    // Outer loop to create parent
    for (let i = 0; i < 3; i++) {
      let children = []
      //Inner loop to create children
      for (let j = 0; j < 3; j++) {
        children.push(<td key={i+"_"+j} onClick={this.tdClick()}>{`${j + 1}`}</td>)
      }
      //Create the parent and add the children
      table.push(<tr key={i}>{children}</tr>)
    }
    return table
  }
  toggleModal=()=>{
    document.querySelector('.screen').classList.toggle("closed");
    document.querySelector('.modal').classList.toggle("closed");
  }
  toggleStart=()=>(e)=>{
    let p1=document.querySelector("#Player1");
    let p2=document.querySelector("#Player2");
    let color1=document.querySelector("#color-select-1").value;
    let color2=document.querySelector("#color-select-2").value;
    let vs=document.querySelector("#vs-select").value;
    if((p1.value===p2.value&&p1.value!=='') || (p1.value==='O' && p2.value==='') || (p2.value==='X' && p1.value==='')){
      
    }else{
      this.toggleModal();
      this.initilizeGame(p1.value,p2.value,color1,color2,false,(vs==="cpu"?true:false));
    }
  }
  toggleMenu=()=>{
    document.querySelector('.modal #start').classList.toggle("closed");
    document.querySelector('.modal #fin').classList.toggle("closed");
  }
  createWinner=()=>{
    const{cpu,player1,player2,clr1,clr2}=this.state;
    let winner=[];
    winner.push(<h2 key="0">Winner is <span id="win"></span></h2>);
    winner.push(<button key="1" onClick={()=>{this.initilizeGame(player1,player2,clr1,clr2,true,cpu);this.toggleModal();}}>Play Again</button>);
    winner.push(<button key="2" onClick={()=>{this.toggleMenu()}}>Reset</button>);
    return (<div id="fin" className="closed">{winner}</div>);
  }
  createOptions=()=>{
    let options=[];
    options.push(<h2 key="0">Player1</h2>);
    options.push(<input id="Player1" placeholder="Default is 'X' symbol" key="0_0" type="text"></input>);
    options.push(<select key="0_0_0" name="color" id="color-select-1">
                 <option className="pink" value="1">Pink</option>
                 <option className="red" value="2">Red</option>
                 </select>);
    options.push(<h2 key="1">Player2</h2>);
    options.push(<input id="Player2" placeholder="Default is 'O' symbol" key="1_1" type="text"></input>);
    options.push(<select key="1_1_1" name="color" id="color-select-2">
                 <option className="green" value="3">Green</option>
                 <option className="yellow" value="4">Yellow</option>
                 </select>);
    options.push(<select key="2_2_2" name="vs" id="vs-select">
    <option className="human" value="human">Human</option>
    <option className="cpu" value="cpu">CPU</option>
    </select>);             
    options.push(<button key="2" onClick={this.toggleStart()}>Play</button>);
    return (<div id="start">{options}</div>);
  }
  initilizeGame=(Player1,Player2,Clr1=1,Clr2=3,Again=false,Cpu=false,GoFirst=true)=>{
    //const{turn,cpu,player1,player2,ttt}=this.state;
    var tds=document.querySelectorAll("td");
    tds.forEach(function(td){
      td.innerHTML="";
      td.classList.remove("flicker1","flicker2","flicker3","flicker4");
    });
    if(Player1==="")Player1='X';
    if(Player2==="")Player2='O';
    this.setState({turn:1,
      goFirst:GoFirst,
      cpu:Cpu,
      player1:Player1,
      player2:Player2,
      clr1:Clr1,
      clr2:Clr2,
      plays:0,
      again:Again,
      ttt:[[0,0,0],[0,0,0],[0,0,0]]});
  }
  //Mount
  componentDidMount() {
    window.addEventListener('load', this.setStyles);
  }
  componentWillUnmount() { 
    window.removeEventListener('load', this.setStyles);
  }
  // Render Function
  render() {
    return(
      <div>
        <div className="sign">Tic Tac Toe</div>
        <div className="screen "></div>
        <div className="modal " id="modal">
          {this.createOptions()}
          {this.createWinner()}
        </div> {/* closed */}
        <table>
        <tbody>
        {this.createTable()}
        </tbody>
      </table>
      </div>

      
    )
  }
}

export default App;
